<template>
  <a-table row-key="id" :columns="columns" :dataSource="data"></a-table>
</template>

<script>
	import ProcessDot from "@/components/ProcessDot/ProcessDot";

	export default {
		name: "OperateRecord1",
		components: {ProcessDot},
		data() {
			return {
				columns: [
					{
