						remark: ''
					},
					{
						key: 'op3',
						type: '',
						name: '',
						status: 'agree',
						updatedAt: '2017-10-03  19:23:12',
						remark: '-'
					},
					{
						key: 'op4',
						type: '',
						name: '',
						status: 'agree',
						updatedAt: '2017-10-03  19:23:12',
						remark: ''
					},
					{
						key: 'op5',
						type: '',
						name: '',
						status: 'agree',
						updatedAt: '2017-10-03  19:23:12',
						remark: '-'
					}
				]
			}
		}
	}
</script>

