<template>
  <div>
    <div class="ant-pro-result">
      <div class="ant-pro-result-head">
        <a-icon type="close-circle" theme="filled" style="background-color: white;color: red;font-size: 72px"/>
        <span class="result-title"></span>
        <span class="result-subtitle"></span>
      </div>

      <div class="ant-pro-result-fail-content">
        <div class="result-content-title"></div>
        <div>
          <a-icon type="close-circle" class="result-content-icon"/>
          <span></span>
          <a style="padding-left: 13px"></a>
          <a-icon type="right" class="result-content-icon"/>
        </div>

        <div>
          <a-icon type="close-circle" class="result-content-icon"/>
          <span></span>
          <a style="padding-left: 13px"></a>
          <a-icon type="right" class="result-content-icon"/>
        </div>
      </div>
      <a-button type="primary" style="display: block;margin: 0 auto 60px;"></a-button>
    </div>
    <page-footer/>
  </div>
</template>

<script>
	import {PageFooter} from "@/components";

	export default {
		name: "Fail",
		components: {PageFooter}
	}
</script>

<style scoped lang="less">
  @import "result";
</style>


