    <div class="profile-page-header">
      <breadcrumb-pro/>
      <div>
        <a-row>
          <a-col v-bind="this.colLayout"></a-col>
          <a-col v-bind="this.colLayout">XX </a-col>
          <a-col v-bind="this.colLayout">2017-07-07</a-col>
          <a-col v-bind="this.colLayout">12421</a-col>
          <a-col v-bind="this.colLayout">2017-07-07 ~ 2017-08-08</a-col>
          <a-col v-bind="this.colLayout"></a-col>
        </a-row>
      </div>
    </div>

    <a-tabs default-active-key="1" class="profile-tabs">
      <a-tab-pane tab="" key="1"></a-tab-pane>
      <a-tab-pane tab="" key="2"></a-tab-pane>
    </a-tabs>

    <div style="margin: 12px 24px">
