            <template slot="description">
              <div style="font-size: 12px; color: rgba(0, 0, 0, 0.45); position: relative; left: 42px;text-align: left;"
                   slot="description">
                <div style="margin: 8px 0 4px">
                  
                  <a-icon style="margin-left: 8px" type="dingding-o"/>
                </div>
                <div>2016-12-12 12:32</div>
              </div>
            </template>
          </a-step>
          <a-step title="">
            <span style="font-size: 14px" slot="title"></span>
            <template slot="description">
              <div style="fontSize: 12px; color: rgba(0, 0, 0, 0.45); position: relative; left: 42px;text-align: left;"
                   slot="description">
                <div style="margin: 8px 0 4px">
                  
                  <a-icon style="margin-left: 8px; color: #00A0E9" type="dingding-o"/>
                </div>
                <div><a href=""></a></div>
              </div>
            </template>
          </a-step>
          <a-step title="">
            <span style="font-size: 14px" slot="title"></span>
          </a-step>
          <a-step title="">
            <span style="font-size: 14px" slot="title"></span>
          </a-step>
        </a-steps>
      </a-card>

      <a-card title="" style="margin-top: 24px">
        <a-row>
          <a-col v-bind="this.colLayout"></a-col>
          <a-col v-bind="this.colLayout">32943898021309809423</a-col>
          <a-col v-bind="this.colLayout">3321944288191034921</a-col>
          <a-col v-bind="this.colLayout">18112345678</a-col>
          <a-col :span="12"> 18100000000 </a-col>
        </a-row>
        <profile-list title="" style="margin-top: 12px">
          <a-row slot="content">
            <a-col v-bind="this.colLayout">725</a-col>
            <a-col v-bind="this.colLayout">2017-08-08</a-col>
            <a-col v-bind="this.colLayout">725</a-col>
            <a-col v-bind="this.colLayout">2017-08-08</a-col>
          </a-row>
        </profile-list>

        <div style="margin-bottom: 12px"></div>
        <a-card title=""  :headStyle="{backgroundColor: '#fafafa'}">
          <profile-list title="">
            <a-row slot="content">
              <a-col v-bind="this.colLayout"></a-col>
              <a-col v-bind="this.colLayout">1234567</a-col>
              <a-col v-bind="this.colLayout">XX - YY</a-col>
              <a-col v-bind="this.colLayout">2017-08-08</a-col>
              <a-col :span="12">...</a-col>
            </a-row>
          </profile-list>

          <a-divider style="margin-bottom: 32px"/>

          <profile-list title="" style="margin-top: 12px">
            <a-row slot="content">
              <a-col :span="24">Citrullus lanatus (Thunb.) Matsum. et Nakai..</a-col>
            </a-row>
          </profile-list>

