<!---->
<template>
  <page-view>
    <div class="ant-pro-profile" slot>
      <profile-list title="">
        <a-row slot="content">
          <a-col v-bind="this.colLayout">1000000000</a-col>
          <a-col v-bind="this.colLayout"></a-col>
          <a-col v-bind="this.colLayout">1234123421</a-col>
          <a-col v-bind="this.colLayout">3214321432</a-col>
        </a-row>
      </profile-list>

      <a-divider style="margin-bottom: 32px"/>

      <profile-list title="">
        <a-row slot="content">
          <a-col v-bind="this.colLayout"></a-col>
          <a-col v-bind="this.colLayout">18100000000</a-col>
          <a-col v-bind="this.colLayout"></a-col>
          <a-col v-bind="this.colLayout">18</a-col>
          <a-col v-bind="this.colLayout"></a-col>
        </a-row>
      </profile-list>

      <a-divider style="margin-bottom: 32px"/>

      <div class="profile-list-table-title"></div>
      <a-table style="margin-bottom: 24px" row-key="id" :columns="goodsColumns" :dataSource="loadGoodsData"></a-table>

      <div class="profile-list-table-title"></div>
      <a-table style="margin-bottom: 24px" row-key="key" :columns="scheduleColumns"
               :dataSource="loadScheduleData"></a-table>
    </div>
  </page-view>
</template>

<script>
	import AListItem from "ant-design-vue/es/list/Item";
	import ProfileList from "@/components/ProfileList/ProfileList";
	import ARow from "ant-design-vue/es/grid/Row";
	import ACol from "ant-design-vue/es/grid/Col";
	import ProcessDot from "@/components/ProcessDot/ProcessDot";
	import PageView from "@/layouts/PageView";

	export default {
		name: "BasicProfile",
		components: {PageView, ProcessDot, ACol, ARow, AListItem, ProfileList},
		data() {
			return {
				colLayout: {
					xs: 8
				},
				goodsColumns: [
					{
						title: '',
						dataIndex: 'id',
						key: 'id',
						customRender: value => <a href="/">{value}</a>
					},
					{
						title: '',
						dataIndex: 'name',
						key: 'name'
					},
					{
						title: '',
						dataIndex: 'barcode',
						key: 'barcode'
					},
					{
						title: '',
						dataIndex: 'price',
						key: 'price',
						align: 'right'
					},
					{
						title: '',
						dataIndex: 'num',
						key: 'num',
						align: 'right'
					},
					{
						title: '',
						dataIndex: 'amount',
						key: 'amount',
						align: 'right'
					}
				],
				//   Promise 
				loadGoodsData: [
					{
						id: '1234561',
						name: ' 550ml',
						barcode: '12421432143214321',
						price: '2.00',
						num: '1',
						amount: '2.00'
					},
					{
						id: '1234562',
						name: ' 300ml',
						barcode: '12421432143214322',
						price: '3.00',
						num: '2',
						amount: '6.00'
					},
					{
						id: '1234563',
						name: '',
						barcode: '12421432143214323',
						price: '7.00',
						num: '4',
						amount: '28.00'
					},
					{
						id: '1234564',
						name: '',
						barcode: '12421432143214324',
						price: '8.50',
						num: '3',
						amount: '25.50'
					}
				],
				scheduleColumns: [
					{
						title: '',
						dataIndex: 'time',
						key: 'time'
					},
					{
						title: '',
						dataIndex: 'rate',
						key: 'rate'
					},
					{
						title: '',
						dataIndex: 'status',
						key: 'status',
						customRender: value => <process-dot status={value}/>
					},
					{
						title: 'ID',
						dataIndex: 'operator',
						key: 'operator'
					},
					{
						title: '',
						dataIndex: 'cost',
						key: 'cost'
					}
				],
				loadScheduleData: [
					{
						key: '1',
						time: '2017-10-01 14:10',
						rate: '',
						status: 'processing',
						operator: ' ID1234',
						cost: '5mins'
					},
					{
						key: '2',
						time: '2017-10-01 14:05',
						rate: '',
						status: 'success',
						operator: ' ID1234',
						cost: '1h'
					},
					{
						key: '3',
						time: '2017-10-01 13:05',
						rate: '',
						status: 'success',
						operator: ' ID1234',
						cost: '5mins'
					},
					{
						key: '4',
						time: '2017-10-01 13:00',
						rate: '',
						status: 'success',
						operator: '',
						cost: '1h'
					},
					{
						key: '5',
						time: '2017-10-01 12:00',
						rate: '',
						status: 'success',
						operator: '',
						cost: '5mins'
					}
				]
			}
		}
	}
</script>

<style scoped lang="less">
  @import "../../common/base";

  .ant-pro-profile {
    padding: 12px;
    margin: 12px;
    background-color: white;
    .profile-list {
      .ant-row > div {
        font-size: 14px;
        line-height: 1.5;
        padding-bottom: 16px;
        color: @fontColor;
      }
    }

    .profile-list-table-title {
      font-size: 16px;
      line-height: 1.5;
      padding-bottom: 16px;
      color: @titleColor;
    }
  }

</style>


