<!---------  --------->
<template>
  <a-card>
    <a-form :form="form" layout="horizontal" @submit="handleSubmit">
      <a-form-item label="" :wrapper-col="wrapperCol" :label-col="labelCol">
        <a-input v-decorator="[
'name',
{rules: [{ required: true, message: '' }]}
]"
                 placeholder=""/>
      </a-form-item>
      <a-form-item label="" :wrapper-col="wrapperCol" :label-col="labelCol">
        <a-range-picker style="width: 100%"
                        v-decorator="[
'beginDate',
{ rules: [{ type: 'array', required: true, message: '' }]}]"/>
      </a-form-item>
      <a-form-item label="" :wrapper-col="wrapperCol" :label-col="labelCol">
        <a-textarea rows="4"
                    placeholder=""
                    v-decorator="['goal', {rules: [{ required: true, message: '' }]}]"/>
      </a-form-item>
      <a-form-item
        label="" :wrapper-col="wrapperCol" :label-col="labelCol">
        <a-textarea
          rows="4"
          placeholder=""
          v-decorator="[
'standard',
{rules: [{ required: true, message: '' }]}
]"/>
      </a-form-item>
      <a-form-item :wrapper-col="wrapperCol" :label-col="labelCol">
<span slot="label">
<span class="tips">()</span>
<a-tooltip title="">
<a-icon type="question-circle-o"/>
</a-tooltip>
</span>
        <a-input
          placeholder=" @"
          v-decorator="['client']"/>
      </a-form-item>
      <a-form-item :wrapper-col="wrapperCol" :label-col="labelCol">
        <span slot="label"><span class="tips">()</span></span>
        <a-input v-decorator="['invites']" placeholder=" @ 5 "/>
      </a-form-item>
      <a-form-item :wrapper-col="wrapperCol" :label-col="labelCol">
        <span slot="label"><span class="tips">()</span></span>
        <a-input-number v-decorator="['weight']" :min="0" :max="100"/>
        <span> %</span>
      </a-form-item>
      <a-form-item label="" :wrapper-col="wrapperCol" :label-col="labelCol" help="">
        <a-radio-group v-model="value">
          <a-radio :value="1"></a-radio>
          <a-radio :value="2"></a-radio>
          <a-radio :value="3"></a-radio>
        </a-radio-group>
        <a-input v-decorator="['publicUsers']" v-if="value===2" placeholder=""/>
      </a-form-item>
      <div style="text-align: center">
        <a-button type="primary" html-type="submit"></a-button>
      </div>
    </a-form>
  </a-card>
</template>

<script>
	import ATextarea from "ant-design-vue/es/input/TextArea";

	export default {
		name: "BasicForm",
		components: {ATextarea},
		data() {
			return {
				description: '',
				wrapperCol: {lg: {span: 10}, md: {span: 14}, sm: {span: 15}},
				labelCol: {lg: {span: 7}, md: {span: 7}, sm: {span: 6}},
				form: this.$form.createForm(this),
				value: 1,
			}
		},
		methods: {
			handleSubmit(e) {
				e.preventDefault();
				this.form.validateFieldsAndScroll((err, values) => {
					if (err) return;

					const params = {
						...values,
						public: this.value,
					};
					console.log(params);
					this.$message.success('');
				});
			}
		}
	}
</script>

<style scoped lang="less">
  .tips {
    color: grey;
  }
</style>


